.userList{
    flex: 5;
    margin-left: 10px;
}

.userListUser {
    display: flex;
    align-items: center;

}





.userTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 10px;
}

.userAddButton{
    width: 80px;
    border: none;
    padding: 5px;
    background-color: teal;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    font-size: 16px;
}

.userContainer{
    display: flex;
    margin-top: 20px;
}







.userListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;

}

.userListEdit {
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #3bb077;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}

.userListDelete {
    color: red;
    cursor: pointer;

}

.uList{
    height: 600px;
}